.project__content {
  margin-bottom: 4rem;
}
.footer.section {
  padding-bottom: 2rem;
}
.footer__inner {
  background: url("../images/contact.png") no-repeat;
  background-position: 50% 80%;
  background-size: 80%;
}
@media all and (min-width: 48em) {
  .footer__inner {
    background-position: center right;
    background-size: 60%;
  }
}
@media all and (min-width: 60.4em) {
  .footer__inner {
    background-size: 70%;
  }
}
.footer__bottom {
  margin-top: 4rem;
}
@media all and (min-width: 72.4em) {
  .footer__bottom {
    margin: 2rem 0 0;
    padding: 0;
  }
}
@media all and (min-width: 48em) {
  .footer.section {
    padding-bottom: 4rem;
  }
  .footer__nav .menu {
    display: flex;
    justify-content: space-between;
    max-width: 60rem;
    font-size: 3.1rem;
  }
}
@media all and (min-width: 48em) and (min-width: 60.4em) {
  .footer__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 3.2rem 0;
  }
  .footer .menu {
    width: 80rem;
  }
}
.header {
  position: absolute;
  padding: 3.2rem;
}
@media all and (min-width: 48em) {
  .header {
    padding: 6.4rem;
  }
}
.header__logo {
  font-family: 'Playfair Display', serif;
  letter-spacing: 0px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-smoothing: antialiased;
  font-weight: 900;
  width: 10rem;
  font-size: 2.2rem;
  line-height: 1.1;
}
@media all and (min-width: 60.4em) {
  .header__logo {
    position: absolute;
    z-index: 11;
    top: 3.2rem;
    left: 3.2rem;
  }
}
@media all and (min-width: 72.4em) {
  .header__logo {
    position: fixed;
  }
}
.container {
  margin: 0 auto;
  position: relative;
  padding: 0 3.2rem;
}
@media all and (min-width: 48em) {
  .container {
    padding: 0 6.4rem;
  }
}
@media all and (min-width: 60.4em) {
  .container {
    max-width: 960px;
    width: 960px;
  }
}
.container.is-fluid {
  margin-left: 3.2rem;
  margin-right: 3.2rem;
  max-width: none;
  width: auto;
}
@media all and (max-width: 72.4em) {
  .container.is-wide {
    max-width: 1152px;
    width: auto;
  }
}
@media all and (max-width: 84.4em) {
  .container.is-fullhd {
    max-width: 1344px;
    width: auto;
  }
}
@media all and (min-width: 72.4em) {
  .container {
    max-width: 1152px;
    width: 1152px;
  }
}
@media all and (min-width: 84.4em) {
  .container {
    max-width: 1344px;
    width: 1344px;
  }
}
.rte.banner__title h1 {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-smoothing: antialiased;
  font-weight: 300;
  font-size: 6vw;
}
@media all and (min-width: 48em) {
  .rte.banner__title h1 {
    font-size: 5.1rem;
  }
}
@media all and (min-width: 60.4em) {
  .rte.banner__title h1 {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-smoothing: antialiased;
    font-weight: 100;
  }
}
@media all and (min-width: 72.4em) {
  .rte.banner__title h1 {
    font-size: 5.2rem;
  }
}
.rte.banner__title h1 strong {
  font-family: 'Playfair Display', serif;
  letter-spacing: 0px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-smoothing: antialiased;
  font-weight: 900;
  font-size: 7vw;
}
@media all and (min-width: 48em) {
  .rte.banner__title h1 strong {
    font-size: 6.1rem;
  }
}
@media all and (min-width: 60.4em) {
  .rte.banner__title h1 strong {
    font-size: 7.2rem;
  }
}
@media all and (min-width: 72.4em) {
  .rte.banner__title h1 strong {
    font-size: 8.5rem;
  }
}
.rte p + p {
  margin-top: 2rem;
}
.section {
  padding: 4.5rem 0 6rem;
  background: #fff;
}
.section--dark {
  background: #251a28;
}
.section__title {
  margin-bottom: 2rem;
}
@media all and (min-width: 60.4em) {
  .section__title {
    margin-bottom: 5rem;
  }
}
.section__subtitle,
.section__teasing {
  margin-bottom: 3rem;
}
.section__content {
  margin-bottom: 4rem;
}
@media all and (min-width: 48em) {
  .section__right {
    padding-left: 3.2rem;
  }
}
@media all and (min-width: 60.4em) {
  .section__right {
    padding-left: 6.4rem;
  }
}
@media all and (min-width: 48em) {
  .section {
    padding: 6.4rem 0 8.4rem;
  }
}
.tpl-styleguide .heading-1 {
  margin-bottom: 4rem;
}
.tpl-styleguide .heading-2 {
  margin-bottom: 0rem;
}
.tpl-styleguide .btn {
  margin: 3rem 0 1rem;
}
.tpl-styleguide .color-title {
  text-transform: uppercase;
}
.tpl-styleguide .color-tile {
  height: 12rem;
  margin: 2rem 0;
  border-radius: 5px;
}
.tpl-styleguide .section {
  margin-bottom: 8rem;
}
.tpl-styleguide .section--typography {
  margin-bottom: 5rem;
}
.tpl-styleguide .section--typography .heading-1,
.tpl-styleguide .section--typography .heading-2,
.tpl-styleguide .section--typography .heading-3,
.tpl-styleguide .section--typography .heading-4 {
  margin-bottom: 0.5rem;
}
.tpl-styleguide .section--typography .teasing-1 {
  margin-bottom: 3rem;
}
